import React, { Component } from 'react'

export default class App extends Component {

  state = {
    name: "jashos",
    isEdit: false,
  }

  componentDidMount() {
    axios.get(`https://jsonplaceholder.typicode.com/users`)
      .then(res => {
        const name = res.data;
        console.log(name);
        // this.setState({ name });
      })
  }

  editInput = () => {
    this.setState({ isEdit: true });
  }
 
  saveInput = () => {
    const { name } = this.state;
    // console.log("name", ...name)
    this.setState({ name, isEdit: false });
  }

  handleChange = (e) => {
    // console.log(e.target.value);
    this.setState({ name: e.target.value});
  }

  handlefocus = (event) => {
    event.target.select();
  }

  render() {
    return (
      <div>
        {!this.state.isEdit ?
          <h1>{this.state.name}</h1> :
          <input onFocus={this.handlefocus} autoFocus onChange={(e) => this.handleChange(e)} type="text" value={this.state.name} />
        }
        {!this.state.isEdit ?
          <button onClick={() => this.editInput()}>Edit</button> :
          <button onClick={() => this.saveInput()}>Save</button>
        }
      </div>
    )
  }
}
